import React from "react";
import { connect } from "react-redux";
import { Redirect, Route, Switch } from "react-router-dom";
import DBService, { DBType } from "../db/DBService";
import { UserObject } from "../db/models";
import { setCurrentOfflineUserAsync } from "../features/users/userSlice";
import MainPage from "../pages/MainPage";
import UsersPage from "../pages/UsersPage";
import Cookies from "../utils/Cookies";
import store, { RootState } from "./store";

type OfflineAppProps = {
    user: UserObject | undefined;
};

type OfflineAppState = {
    isDbLoading: boolean;
};

const mapStateToProps = (state: RootState) => {
    return {
        user: state.user.currentUser
    };
};

class OfflineAppInner extends React.Component<
    OfflineAppProps,
    OfflineAppState
> {
    constructor(props: OfflineAppProps) {
        super(props);

        this.state = {
            isDbLoading: true
        };

        this.onDBReady = this.onDBReady.bind(this);
    }

    async componentDidMount(): Promise<void> {
        await DBService.start(DBType.Offline);
        if (DBService.db) {
            DBService.db.onReady(this.onDBReady);
        }
    }

    async onDBReady(): Promise<void> {
        try {
            if (DBService.db) await DBService.db.seedDatabase();

            const userId = Cookies.getCookie("user_id");
            if (userId) {
                await store.dispatch(setCurrentOfflineUserAsync(userId));
                Cookies.setCookie("user_id", userId);
            }

            this.setState({ isDbLoading: false });
        } catch (e) {
            console.log(e);
        }
    }

    render(): JSX.Element | null {
        const { user } = this.props;
        const { isDbLoading } = this.state;

        if (isDbLoading) return null;

        return (
            <>
                <Switch>
                    <Route path="/main" component={MainPage} />
                    <Route path="/users" component={UsersPage} />
                </Switch>
                {(user && <Redirect to="/main" />) ||
                    (!user && <Redirect to="/users" />)}
            </>
        );
    }
}

const OfflineApp = connect(mapStateToProps)(OfflineAppInner);
export default OfflineApp;
