const groupbyDataFrame: NodeType = {
    id: "groupby-dataframe",
    name: "Group By",
    description: Descriptions.NodeSpecific.GroupByDataFrame.Description,
    style: {
        width: 160
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.Common.DataFrameInput,
            type: Types.DataFrame
        }
    ],
    outputSlots: [
        {
            id: "result",
            description: Descriptions.Common.ResultOutput,
            type: Types.DataFrame
        }
    ],
    properties: [
        {
            id: "groupColumns",
            description:
                Descriptions.Common.DataFrameGrouping.GroupColumnsProperty,
            type: "string",
            regex: Regexes.labelCommaRegex
        },
        {
            id: "keys",
            description:
                Descriptions.NodeSpecific.GroupByDataFrame.KeysProperty,
            type: "string",
            regex: Regexes.valueCommaRegex
        }
    ],
    nodeFunction: async (
        input: PrimitiveTree,
        properties: PrimitiveObject | undefined,
        errorHandler: NodeErrorHandler | undefined
    ): Promise<PrimitiveTree> => {
        if (!errorHandler) return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input"])) {
            errorHandler.error(Errors.MissingNodeInput("input"));
            return {};
        }

        const df = input["input"] as DataFrame;
        const columns = properties["groupColumns"] as string;
        const keys = properties["keys"] as string;

        let columnsArr: string[] | undefined;
        if (Regexes.isMatch(columns, Regexes.labelCommaRegex))
            columnsArr = Regexes.processCommaDelimString(
                columns,
                Regexes.baseLabelRegex
            );

        let keysArr: string[] | undefined;
        if (Regexes.isMatch(keys, Regexes.valueCommaRegex))
            keysArr = Regexes.processCommaDelimString(
                keys,
                Regexes.baseValueRegex
            );

        if (!columnsArr || columnsArr.length <= 0)
            errorHandler.warning(
                Errors.EmptyNodePropertyNoEffect("groupColumns")
            );

        if (!keysArr || keysArr.length <= 0)
            errorHandler.warning(Errors.EmptyNodePropertyNoEffect("keys"));

        if (columnsArr && keysArr && columnsArr.length !== keysArr.length)
            errorHandler.error(
                Errors.ArrayLengthsNotEqual("groupColumns", "keys")
            );

        if (
            !columnsArr ||
            columnsArr.length <= 0 ||
            !keysArr ||
            keysArr.length <= 0 ||
            (columnsArr && keysArr && columnsArr.length !== keysArr.length)
        )
            return {
                result: df
            };

        const invalidValues: string[] = [];
        columnsArr.forEach((col, index) => {
            const colIndex = df.columns.indexOf(col);
            if (colIndex < 0) {
                errorHandler.error(Errors.ValueNotExists(col, "column"));
                invalidValues.push(col);
                return;
            }

            if (!keysArr) return;

            const type = df.dtypes[colIndex];
            let key: string | number | boolean = keysArr[index];
            if (type === "float32" || type === "int32") key = Number(key);
            else if (type === "boolean")
                key = key.toLowerCase() === "true" || key === "1";

            const dfUniques = df.column(col).unique().values as ArrayType1D;
            if (!dfUniques.includes(key)) {
                errorHandler.error(
                    Errors.ValueNotExists(keysArr[index], "key")
                );
                invalidValues.push(keysArr[index]);
            }
        });

        if (invalidValues.length > 0)
            return {
                result: df
            };

        const group = DanfoUtils.groupby(df, columnsArr);
        const newDf = group.getGroup(keysArr) as DataFrame;
        return {
            result: newDf
        };
    }
};

const groupCountDataFrame: NodeType = {
    id: "group-count-dataframe",
    name: "Group Count",
    description: Descriptions.NodeSpecific.GroupCountDataFrame.Description,
    style: {
        width: 160
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.Common.DataFrameInput,
            type: Types.DataFrame
        }
    ],
    outputSlots: [
        {
            id: "result",
            description: Descriptions.Common.ResultOutput,
            type: Types.DataFrame
        }
    ],
    properties: [
        {
            id: "groupColumns",
            description:
                Descriptions.Common.DataFrameGrouping.GroupColumnsProperty,
            type: "string",
            regex: Regexes.labelCommaRegex
        },
        {
            id: "isPercent",
            description:
                Descriptions.NodeSpecific.GroupCountDataFrame.IsPercentProperty,
            type: "boolean",
            defaultValue: false
        }
    ],
    nodeFunction: async (
        input: PrimitiveTree,
        properties: PrimitiveObject | undefined,
        errorHandler: NodeErrorHandler | undefined
    ): Promise<PrimitiveTree> => {
        if (!errorHandler) return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input"])) {
            errorHandler.error(Errors.MissingNodeInput("input"));
            return {};
        }

        const df = input["input"] as DataFrame;
        const groupColumns = properties["groupColumns"] as string;
        const isPercent = properties["isPercent"] as boolean;

        let groupColumnsArr: string[] | undefined;
        if (Regexes.isMatch(groupColumns, Regexes.labelCommaRegex))
            groupColumnsArr = Regexes.processCommaDelimString(
                groupColumns,
                Regexes.baseLabelRegex
            );

        if (!groupColumnsArr || groupColumnsArr.length <= 0)
            errorHandler.warning(
                Errors.EmptyNodePropertyNoEffect("groupColumns")
            );

        if (!groupColumnsArr || groupColumnsArr.length <= 0)
            return {
                result: df
            };

        const invalidValues: string[] = [];
        groupColumnsArr.forEach((col) => {
            if (!df.columns.includes(col)) {
                errorHandler.error(Errors.ValueNotExists(col, "group column"));
                invalidValues.push(col);
            }
        });

        if (invalidValues.length > 0)
            return {
                result: df
            };

        const group = DanfoUtils.groupby(df, groupColumnsArr);
        const newDf = group.col([groupColumnsArr[0]]).count() as DataFrame;
        newDf.rename(
            { [`${groupColumnsArr[0]}_count`]: "Count" },
            { inplace: true }
        );

        if (isPercent) {
            let countCol = newDf.column("Count");
            const total = countCol.sum();
            countCol = countCol.div(total) as Series;
            newDf.addColumn("Count", countCol.values as ArrayType1D, {
                inplace: true
            });
        }

        return {
            result: newDf
        };
    }
};

